import React from "react";
import "./HeroSection.css";

function HeroSection({
  lightBg,
  topLine,
  lightText,
  lightTextDesc,
  headline,
  description,
  img,
  alt,
  imgStart,
}) {
  return (
    <>
      <div className={lightBg ? "profile" : "profile darkBg"}>
        <div className="profile-container">
          <div
            className="row-profile profile-hero-row"
            style={{
              display: "flex",
              flexDirection:
                imgStart === "start" ? "row-reverse" : "row-profile",
            }}
          >
            <div className="col-profile">
              <div className="profile-text-wrapper">
                <div className="top-line">{topLine}</div>
                <h1 className={lightText ? "heading" : "heading dark"}>
                  {headline}
                </h1>
                <p
                  className={
                    lightTextDesc ? "profile-subtitle" : "profile-subtitle dark"
                  }
                >
                  {description}
                </p>
              </div>
            </div>
            <div className="col-profile">
              <div className="profile-img-wrapper">
                <img src={img} alt={alt} className="profile-img" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default HeroSection;
